import React from "react";
import { useDrop } from "react-dnd";
import { ItemTypes } from "./items";


export const DropWrapper = ({ onDrop, board, taskOwner, columnTitle, tasks, taskOrder, columns, columnIndex, children }) => { 
    
//------------------- dnd (–ø–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∏ –º—ñ–∂ –∫–æ–ª–æ–Ω–∫–∞–º–∏) -------------------------
    const [{ isOver, canDrop }, drop] = useDrop({
        accept: ItemTypes.CARD,
        canDrop: (item, monitor) => {
            // –∑–Ω–∞—Ö–æ–¥–∏–º–æ —ñ–Ω–¥–µ–∫—Å –∫–æ–ª–æ–Ω–∫–∏, –∑ —è–∫–æ—ó —Ç—è–≥–Ω–µ–º–æ
            const columnIndexWithItem = columns.findIndex(column => column._id === item.taskOwner); // —ñ–Ω–¥–µ–∫—Å –∞–±–æ "-1"
            return [columnIndexWithItem + 1, columnIndexWithItem - 1, columnIndexWithItem].includes(columnIndex); 
        },

        // —Ç—É—Ç –±–µ–∑ hover, —ñ–Ω–∞–∫—à–µ –≤–∏–∫–æ–Ω—É–≤–∞—Ç–∏–º–µ—Ç—å—Å—è –Ω–∞ –∫–æ–∂–Ω–æ–º—É –º—ñ–ª—ñ–º–µ—Ç—Ä—ñ –±—É–¥—å-—è–∫–æ—ó –∫–æ–ª–æ–Ω–∫–∏, —Ç–æ–º—É —â–æ ref –Ω–∞ –∫–æ–ª–æ–Ω—Ü—ñ, –∞ –Ω–µ –Ω–∞ —Ç–∞—Å—Ü—ñ
        drop: (item, monitor) => {
            onDrop(item, monitor, board, taskOwner, columnTitle, tasks, taskOrder, columnIndex); // —Ñ-—Ü—ñ—è onDrop –∑–Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è –≤ MainDashboard 
            // –≤ tasks –ø–æ—Ç—Ä–∞–ø–ª—è—é—Ç—å —Ç–∞—Å–∫–∏ —Ç—ñ–ª—å–∫–∏ —Ç—ñ—î—ó –∫–æ–ª–æ–Ω–∫–∏, –Ω–∞ —è–∫—É –º–∏ –∫–∏–¥–∞—î–º–æ –∫–∞—Ä—Ç–∫—É 
            // –≤ columnId –ø–æ—Ç—Ä–∞–ø–ª—è—î id —Ç—ñ–ª—å–∫–∏ —Ç—ñ—î—ó –∫–æ–ª–æ–Ω–∫–∏, –Ω–∞ —è–∫—É –º–∏ –∫–∏–¥–∞—î–º–æ –∫–∞—Ä—Ç–∫—É 
        },  
        collect: monitor => ({
            isOver: monitor.isOver(),
            canDrop: !!monitor.canDrop()
        })
    });
    
    return (
        <div ref={drop}>
            <div 
                style={{ 
                    backgroundColor: isOver && canDrop ? 'rgba(43, 255, 0, 0.075)' : isOver && !canDrop ? 'rgba(233, 14, 14, 0.178)' : 'initial', 
                    borderRadius: '8px', 
                    transition: '250ms cubic-bezier(0.4, 0, 0.2, 1) 0s'
                }}>
                {children} 
            </div>
        </div>
    )
};