import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import update from 'immutability-helper'; // dnd
import { editColumn } from '../../redux/board/boardOperations'; 
import { Card } from 'components/Card';
import styles from 'styles/index.module.scss';
import { useBoards } from 'hooks';


export const TasksList = ({ tasks, idColumn:_id, columnTitle, getReoderedCardsIds }) => {
  const dispatch = useDispatch();
  const {  filter: priority } = useBoards();

  //-------------------------------------------- dnd ---------------------------------------------------------------

  // 1. –ü–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∏ –≤ —ñ–Ω—à—É –∫–æ–ª–æ–Ω–∫—É:
  // 1.1. –ù–∞ –º—ñ—Å—Ü–µ —ñ–Ω—à–æ—ó –∫–∞—Ä—Ç–∫–∏, —è–∫—É —Ç—Ä–µ–±–∞ –æ–ø—É—Å—Ç–∏—Ç–∏ (–∫—É—Ä—Å–æ—Ä –Ω–∞ –≤–µ—Ä—Ö–Ω—ñ–π —á–∞—Å—Ç–∏–Ω—ñ —Å—Ç–∞—Ç–∏—á–Ω–æ—ó –∫–∞—Ä—Ç–∫–∏) 
  
  const getReoderedCardsIdxs = (reoderedTasks) => reoderedTasks.map((card) => card._id); // –ø—ñ—Å–ª—è –ø–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è —Ç–∞—Å–∫–∏ —Ç—Ä–µ–±–∞ –∑–º—ñ–Ω–∏—Ç–∏ taskOrder
  
  const moveToAnotherColumn = useCallback((hoverIndex, item) => { // –≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –≤ Card 
    // hoverIndex - –ø–æ–∑–∏—Ü—ñ—è, –Ω–∞ —è–∫—É –ø–µ—Ä–µ—Ç—è–≥–ª–∏ –∫–∞—Ä—Ç–∫—É
      const newItem = {...item, _id: item.idTask}; 
      delete newItem.idTask;
      delete newItem.index;

      const reoderedTasks = update([...tasks], { //  –ø–æ–≤–µ—Ä—Ç–∞—î –º–∞—Å–∏–≤ –ø–µ—Ä–µ—Ç–∞—Å–æ–≤–∞–Ω–∏—Ö —Ç–∞—Å–æ–∫ 
        $splice: [ 
          [hoverIndex, 0, newItem],
        ],
      });  
      getReoderedCardsIds({ _id, taskOrder: getReoderedCardsIdxs(reoderedTasks) });
      // –ø—ñ—Å–ª—è –∫–∏–¥–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∏ –∑–∞–ø—É—Å–∫–∞—î—Ç—å—Å—è useDrop (–≤ DropWrapper) => onDrop (–≤ MainDashboard) 
  }, [tasks, _id, getReoderedCardsIds]);


  // 1.2. –ù–∞ –º—ñ—Å—Ü–µ —ñ–Ω—à–æ—ó –∫–∞—Ä—Ç–∫–∏, —è–∫–∞ –º–∞—î –∑–±–µ—Ä–µ–≥—Ç–∏ —Å–≤–æ—é –ø–æ–∑–∏—Ü—ñ—é, –∞ –Ω–æ–≤—É —Ç—Ä–µ–±–∞ –æ–ø—É—Å—Ç–∏—Ç–∏ (–∫—É—Ä—Å–æ—Ä –Ω–∞ –Ω–∏–∂–Ω—ñ–π —á–∞—Å—Ç–∏–Ω—ñ —Å—Ç–∞—Ç–∏—á–Ω–æ—ó –∫–∞—Ä—Ç–∫–∏) 
  const moveToAnotherColumnUnder = useCallback((hoverIndex, item) => { 
    const newItem = {...item, _id: item.idTask}; 
    delete newItem.idTask;
    delete newItem.index;

    const reoderedTasks = update([...tasks], { 
      $splice: [ 
        [hoverIndex + 1, 0, newItem],
      ],
    });
    getReoderedCardsIds({ _id, taskOrder: getReoderedCardsIdxs(reoderedTasks) });
    // –ø—ñ—Å–ª—è –∫–∏–¥–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∏ –∑–∞–ø—É—Å–∫–∞—î—Ç—å—Å—è useDrop (–≤ DropWrapper) => onDrop (–≤ MainDashboard) 
  }, [tasks, _id, getReoderedCardsIds]);


  // 2. –ü–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è —Ç–∞—Å–∫–∏ (–≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ) –≤ –º–µ–∂–∞—Ö 1 –∫–æ–ª–æ–Ω–∫–∏
  const moveInsideColumn = useCallback((dragIndex, hoverIndex) => { 
    // dragIndex - –º—ñ—Å—Ü–µ, –∑ —è–∫–æ–≥–æ –∑—ñ—Ä–≤–∞–ª–∏ –∫–∞—Ä—Ç–∫—É —ñ —Ç—è–≥–Ω–µ–º–æ 
    // hoverIndex - –ø–æ–∑–∏—Ü—ñ—è, –Ω–∞–¥ —è–∫–æ—é —É—Ç—Ä–∏–º—É—î–º–æ –∫–∞—Ä—Ç–∫—É
      const newTasks = [...tasks]; 
      const reoderedTasks = update(newTasks, { 
        $splice: [  
          [dragIndex, 1],
          [hoverIndex, 0, newTasks[dragIndex]], 
        ],
      });
      dispatch(editColumn({ idColumn: _id, title: columnTitle, taskOrder: getReoderedCardsIdxs(reoderedTasks) }))  
  }, [tasks, _id, columnTitle, dispatch]);


  const filteredTasks =
    priority === 'all'
      ? tasks
      : tasks.filter(task => {
          return task?.priority === priority;
        });

  const renderCard = useCallback(({ title, description, priority, deadLine, _id, taskOwner}, index) => {
    return (
      <li key={_id}> 
        <Card
          columnTitle={columnTitle}
          cardTitle={title}
          description={description}
          priority={priority}
          deadline={deadLine}
          id={_id}
          taskOwner={taskOwner}
          index={index} // –¥–ª—è dnd 
          moveToAnotherColumn={moveToAnotherColumn} // –¥–ª—è dnd
          moveToAnotherColumnUnder={moveToAnotherColumnUnder} // –¥–ª—è dnd
          moveInsideColumn={moveInsideColumn}
        />
      </li>
    );
  }, [columnTitle, moveToAnotherColumn, moveToAnotherColumnUnder, moveInsideColumn]);

  return (
    <ul className={styles.KkCards}>
        {filteredTasks.length !== 0 &&
          filteredTasks.map((task, index) => renderCard(task, index))
        }
      </ul>
  );
}